
.top .light {
	position: absolute;
	left: 23px;
	top: 33px;
	font-size: 18px;
	color: #FFF;
}
.top>img {
	width: 100%;
	height: 135px;
}
.top>h2 {
	position: absolute;
	left: 23px;
	top: 62px;
	font-size: 25px;
	font-weight: 600;
	color: #FFF;
	font-family: "微软雅黑";
}
.titleLp {
	width: 100%;
	min-height: 100px;
	margin-bottom: 20px;
}
.titleLp .title {
	width: 100%;
	font-size: 16px;
	font-weight: bold;
	color: #333333;
	text-indent: 10px;
	background: #FFF;
	border-top-left-radius: 2px;
	border-top-right-radius: 2px;
	padding-top: 14px;
}
.titleLp .title .lightnote {
	font-size: 14px;
	color: #666;
	background: #FFF;
	float: right;
	padding-right: 15px;
}
.titleLp .title .lightnote>i{
	font-style: normal;
	font-family: "微软雅黑";
	color: #ccc;
}
.titleLp .title .lightnote i>img{
	width: 6px;
	height: 12px;
	position: relative;
	top: -1px;
}
.titleLp .indec {
	font-size: 12px;
	color: #999;
	background: #FFF;
	text-indent: 10px;
	padding: 4px 0 12px 0;
}
/*底部线*/
.borderbottom {
	position: relative;
}
.borderbottom:after {
	content: '';
	position: absolute;
	left: 11px;
	bottom: 0;
	width: 94%;
	height: 1px;
	background-color: #EDEDED;
	transform: scaleY(.5)
}
/*定位字段*/
.locationinfo {
	width: 100%;
	height: 37px;
	background: #fff7dc;
	box-sizing: border-box;
	padding: 0 10px 0 16px;
}
.noinfo {
	color: #9a7126;
	font-size: 12px;
	width: 100%;
	height: 37px;
	line-height: 37px;
}
.setting {
	width: 70px;
	color: #666;
	font-size: 12px;
	height: 24px;
	text-align: center;
	line-height: 24px;
	float: right;
	margin-right: 10px;
	border: 1px solid #e4e4e4;
	border-radius: 2px;
	background: #FFF;
	position: absolute;
	right: 0px;
	top: 5px
}
/*蒙城弹框*/
.layer {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background: rgba(0, 0, 0, 0.8);
}
.layer .inlayer {
	width: 320px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	-webkit-transform: translate(-50%, -50%);
	background: #FFF;
	border-radius: 4px;
	padding-bottom: 20px;
}
.layer .rules {
	box-sizing: border-box;
	padding: 0 25px 30px 25px;
}
.layer .inlayer .light {
	position: absolute;
	top: 34px;
	left: 26px;
	font-size: 21px;
	color: #FFF;
}
.layer .inlayer .ruletitle {
	text-align: center;
	padding: 25px 0 20px 0;
	font-size: 18px;
	color: #333;
}
.layer .inlayer .rule {
	font-size: 14px;
	color: #333;
	margin-bottom: 16px;
}
.layer .layerpic {
	height: 200px;
	width: 100%;
	border-radius: 4px;
}
.layer .layerpic>img {
	height: 200px;
	width: 100%;
	border-radius: 4px;
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;
}
.layer .layerTitle {
	position: absolute;
	top: 65px;
	left: 26px;
	font-size: 30px;
	color: #FFF;
	font-family: "微软雅黑";
	font-weight: bold;
}
.layer .layerdec {
	padding-top: 15px;
	text-align: center;
	font-size: 14px;
	color: #666;
}
.layer .layerdec1 {
	padding-top: 2px;
	text-align: center;
	font-size: 14px;
	color: #666;
}
.layer .look {
	width: 150px;
	height: 34px;
	border-radius: 99px;
	background: #FF5346;
	text-align: center;
	line-height: 33px;
	font-size: 18px;
	color: #FFF;
	margin: 10px auto 0;
}
.close {
	position: absolute;
	right: 0;
	top: -63px;
	width: 30px;
}



